@import "breakpoints.scss";
@import "mixins.scss";

.cart-summary {
  display: flex;
  flex-direction: column;
  text-align: center;
  max-width: 100rem;
  margin: 15rem auto;
  box-shadow: 1px 1px 10px #ddd;
  border-radius: 1rem;

  &__header {
    width: 100%;
    background: #ddd;
    padding: 1rem;
    border-radius: 1rem 1rem 0 0;
  }

  &__title {
    font-size: 1.6rem;
    font-weight: 600;
  }

  &__info {
    width: 100%;
    text-align: center;
    font-size: 1.4rem;
    padding: 1rem;
  }

  &__products {
    display: flex;
    flex-direction: column;
  }

  &__footer {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #ddd;
    padding: 2rem;
    border-radius: 0 0 1rem 1rem;
  }

  &__total {
    font-size: 1.2rem;
    @include mq(smartphone) {
      font-size: 1.6rem;
    }
  }

  &__total-price {
    font-size: 1.4rem;
    font-weight: bold;
    @include mq(smartphone) {
      font-size: 2rem;
    }
  }
}
