@import "breakpoints.scss";
@import "mixins.scss";

.subcart-item {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: nowrap;
  padding: 1.5rem;

  &__img {
    width: 5rem;
  }

  &__title {
    font-size: 1.6rem;
    padding: 0 1rem;
    display: none;
    @include mq(smartphone) {
      display: block;
    }
  }

  &__quantity,
  &__price {
    font-size: 1.4rem;
    display: flex;
  }
}
